.footer {
    display: flex;
    justify-content: space-between;
    padding-top: 50px;
    align-items: center;
}

.footer__list {
    font-family: var(--font-headlines-family);
    font-size: var(--font-headlines-6-size);
    line-height: var(--font-headlines-6-height);
    font-weight: 500;
}

.footer__item {
    font-family: var(--font-headlines-family);
    font-size: var(--font-body-2-size);
    line-height: var(--font-body-2-height);
    font-weight: 400;
}

.contact {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.contact__icons {
    display: flex;
    gap: 10px;
    justify-content: center;
}

.contact__icon {
width: 30px;
}

.footer__link {
    font-family: var(--font-body-family);
    font-size: var(--font-body-2-size);
    line-height: var(--font-body-3-height);
    font-weight: 400;
}